<script>
import { PackageAPI, AccountAPI } from "@/scripts/ApiUtil"

import { get } from 'axios'

export default {
	name: "index",
	data() {
		return {
			promoData: null,
			packageData: null,
			currentAccountData: null
		}
	},
	async created() {
		let _promoData = await get("/promo.json");
		if (_promoData.status == 200) {
			this.promoData = _promoData.data;
		} else {
			this.promoData = {
				status: _promoData.status
			}
		}
		
		let _packageData = await PackageAPI.getPackages();
		if (_packageData.status == 200) {
			this.packageData = _packageData.data;
		} else {
			this.packageData = {
				status: _packageData.status
			}
		}
		
		// let _accountData = await AccountAPI.getMe();
		// if (_accountData.status == 200) {
		// 	this.currentAccountData = _accountData.data;
		// }
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		getInitials(name) {
			var initials = name.replace(/\_|\:|\.|\:/g, " ").match(/\b\w/g) || [];
			initials = ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
			
			return initials;
		}
	}
}
</script>