var storeUINavigationBar = {
	name: "storeui-navbar",
	props: ["title"],
	data() {
		return {
			scrollHeight: null
		}
	},
	mounted() {
		this.$refs["contentView"].className = "navigation-bar-content-view invisible hidden";
		this.scrollHeight = this.$refs["largeTitleLabel"].clientHeight;
		this.$refs["container"].style.height = `${this.scrollHeight + 44}px`;
		this.$refs["container"].classList.add("no-separator");
		
		if (document.querySelector(".collection-view")) {
			document.querySelector(".collection-view").style.paddingTop = `${this.scrollHeight + 44}px`;
		}
		
		window.addEventListener("scroll", this.didScroll);
		
		if (document.body.scrollTop > this.scrollHeight) {
			this.$refs["container"].classList.remove("no-separator");
			this.$refs["largeTitleView"].classList.add("invisible");
			this.$refs["contentView"].classList.remove("hidden");
			this.$refs["contentView"].classList.remove("invisible");
	
			this.$refs["container"].style.transform = `translate3d(0, -52px, 0)`;
			this.$refs["largeTitleView"].style.transform = `translate3d(0, 52px, 0)`;
			this.$refs["largeTitleAnimationWrapper"].style.transform = `translate3d(0, -52px, 0)`;
			this.$refs["contentView"].style.transform = `translate3d(0, 52px, 0)`;
		}
	},
	destroyed() {
		window.removeEventListener("scroll", this.didScroll);
	},
	render(h) {
		return (
			<div class="navigation-bar" ref="container">
				<div class="navigation-bar-large-title-view" ref="largeTitleView">
					<div class="animation-wrapper" ref="largeTitleAnimationWrapper">
						<p class="navigation-bar-title" ref="largeTitleLabel">{ this.$props.title }</p>
						{ this.$slots.default }
					</div>
				</div>
				<div class="navigation-bar-content-view" ref="contentView">
					<p class="navigation-bar-title" ref="contentLabel">{ this.$props.title }</p>
				</div>
			</div>
		)
	},
	methods: {
		didScroll() {
			if (document.body.scrollTop >= 0 && document.body.scrollTop <= this.scrollHeight) {
				this.$refs["container"].classList.add("no-separator");
				this.$refs["largeTitleView"].classList.remove("invisible");
				this.$refs["contentView"].classList.add("invisible");
				
				this.$refs["container"].style.height = `${96}px`;
				this.$refs["container"].style.transform = `translate3d(0, -${document.body.scrollTop}px, 0)`;
				this.$refs["largeTitleLabel"].style.transform = ``;
				this.$refs["largeTitleView"].style.transform = `translate3d(0, ${document.body.scrollTop}px, 0)`;
				this.$refs["largeTitleAnimationWrapper"].style.transform = `translate3d(0, -${document.body.scrollTop}px, 0)`;
				this.$refs["contentView"].style.transform = `translate3d(0, ${document.body.scrollTop}px, 0)`;
			} else if (document.body.scrollTop < 0) {
				this.$refs["container"].classList.add("no-separator");
				this.$refs["largeTitleView"].classList.remove("invisible");
				this.$refs["contentView"].classList.add("invisible");
				
				this.$refs["container"].style.height = `${96 - document.body.scrollTop}px`;
				this.$refs["container"].style.transform = `translate3d(0, 0, 0)`;
				
				if (document.body.scrollTop >= -84) {
					this.$refs["largeTitleLabel"].style.transform = `scale(${1 + (0.1 * (document.body.scrollTop / -84))})`;
				} else {
					this.$refs["largeTitleLabel"].style.transform = `scale(1.1)`;
				}
				
				this.$refs["largeTitleView"].style.transform = `translate3d(0, ${-document.body.scrollTop}px, 0)`;
				this.$refs["largeTitleAnimationWrapper"].style.transform = `translate3d(0, 0, 0)`;
				this.$refs["contentView"].style.transform = `translate3d(0, 0, 0)`;
			} else if (document.body.scrollTop > this.scrollHeight) {
				this.$refs["container"].classList.remove("no-separator");
				this.$refs["largeTitleView"].classList.add("invisible");
				this.$refs["contentView"].classList.remove("hidden");
				this.$refs["contentView"].classList.remove("invisible");
		
				this.$refs["container"].style.height = `${96}px`;
				this.$refs["container"].style.transform = `translate3d(0, -${this.scrollHeight}px, 0)`;
				this.$refs["largeTitleLabel"].style.transform = ``;
				this.$refs["largeTitleView"].style.transform = `translate3d(0, ${this.scrollHeight}px, 0)`;
				this.$refs["largeTitleAnimationWrapper"].style.transform = `translate3d(0, -${this.scrollHeight}px, 0)`;
				this.$refs["contentView"].style.transform = `translate3d(0, ${this.scrollHeight}px, 0)`;
			}
		}
	}
}

var storeUIHorizontalCollectionViewCell = {
	name: "storeui-collection-view-cell",
	props: ["type"],
	data() {
		return {
			swiper: null
		}
	},
	mounted() {
		if (this.$props.type) {
			let defaultOptions = this.defaultOptions()[this.$props.type];
			
			let options = {
				roundLengths: 'false',
				spaceBetween: 32,
				breakpoints: {
					414: {
						spaceBetween: 10,
						slidesPerView: 1
					},
					768: {
						spaceBetween: 20
					},
					834: {
						spaceBetween: 22
					},
					1024: {
						spaceBetween: 30
					}
				}
			}
			
			this.swiper = new Swiper(this.$refs["container"], Object.assign({
				roundLengths: 'false',
				spaceBetween: 32,
				breakpoints: {
					414: {
						spaceBetween: 10,
						slidesPerView: 1
					},
					768: {
						spaceBetween: 20
					},
					834: {
						spaceBetween: 22
					},
					1024: {
						spaceBetween: 30
					}
				}
			}, this.defaultOptions()[this.$props.type]));
		}
	},
	updated() {
		this.swiper.update();
	},
	render(h) {
		return (
			<div class="horizontal-collection-view-cell swiper-container" ref="container">
				<div class="swiper-wrapper">
					{ this.$slots.default }
				</div>
			</div>
		)
	},
	methods: {
		defaultOptions() {
			return {
				editorial: {
					slidesPerView :'auto',
					slidesPerViewFit : false,
				},
				small: {
					slidesPerView: 'auto',
					slidesPerColumn: 3,
					slidesPerGroup: 1,
				},
				medium: {
					slidesPerView: 'auto',
					slidesPerColumn: 2,
					slidesPerGroup: 1,
				},
				brick: {
					slidesPerView :'auto',
					slidesPerViewFit : false,
					breakpoints: {
						414: {
							spaceBetween: 10,
						},
						768: {
							spaceBetween: 20
						},
						834: {
							spaceBetween: 22
						},
						1024: {
							spaceBetween: 30
						}
					}
				},
				media: {
					slidesPerView :'auto',
					slidesPerViewFit : false,
					breakpoints: {
						414: {
							spaceBetween: 10,
						},
						768: {
							spaceBetween: 20
						},
						834: {
							spaceBetween: 22
						},
						1024: {
							spaceBetween: 30
						}
					}
				},
				review: {
					slidesPerView :'auto',
					slidesPerViewFit : false,
				}
			}
		}
	}
}

var storeUIExpandableText = {
	name: "storeui-expandable-text",
	props: ["hideEmptyLines", "maxLines", "startExpanded", "redirectURI", "targetBlank"],
	data() {
		return {
			_startExpanded: this.$props.startExpanded !== undefined ? this.$props.startExpanded :false,
			_hideEmptyLines: this.$props.hideEmptyLines !== undefined ? this.$props.hideEmptyLines : true,
			_maxLines: this.$props.maxLines !== undefined ? this.$props.maxLines :3,
			_redirectURI: this.$props.redirectURI !== undefined ? this.$props.redirectURI : null,
			_targetBlank: this.$props.targetBlank !== undefined ? this.$props.targetBlank : false,
			
			_rawText: "",
			expanded: false
		}
	},
	mounted() {
		this.$data._rawText = this.$el.querySelector("p").innerHTML;
		this.$el.style.maxHeight = `${this.$data._maxLines * 19}px`;
		this.$el.querySelector("p").innerHTML = this.getText();
		
		this.checkMoreButtonPresence();
		window.addEventListener("resize", this.checkMoreButtonPresence.bind(this));
	},
	render(h) {
		return (
			<div class="expandable-text-view">
				{ this.$slots.default }
				<button class="fade-in-button" ref="fadeInButton" onClick={this.expand}>More</button>
			</div>
		)
	},
	methods: {
		checkMoreButtonPresence() {
			if (this.$data._startExpanded) {
				this.$el.classList.add("expanded");
				this.$el.removeAttribute("style");
			}
			
			if (this.$el.scrollHeight > (19 * this.$data._maxLines) && !this.$data._startExpanded && !this.$data._expanded) {
				this.$refs["fadeInButton"].classList.remove("hidden");
			} else {
				this.$refs["fadeInButton"].classList.add("hidden");
			}
		},
		expand() {
			if (this.$data._redirectURI) {
				if (this.$data._targetBlank) {
					let route = this.$router.resolve({ path: this.$data._redirectURI });
					window.open(route.href, '_blank');
					
					return;
				}
				
				this.$router.push(this.$data._redirectURI);
				return;
			}
			
			this.$data._expanded = true;
		
			this.$el.classList.add("expanded");
			this.$el.removeAttribute("style");
			this.$refs["fadeInButton"].classList.add("hidden");
			
			this.$el.querySelector("p").innerHTML = this.getText();
		},
		getText() {
			if (this.$data._hideEmptyLines && !this.$data._startExpanded && !this.$data._expanded) {
				return this.$data._rawText.replace(/(\<br\>\<br\>|\n\n)/g, "<br>");
			} else {
				return this.$data._rawText;
			}
		}
	}
}

var storeUIAnnotationCell = {
	name: "storeui-annotation-cell",
	props: ["expandable"],
	data() {
		return {
			_expandable: this.$props.expandable !== undefined ? this.$props.expandable : false
		}
	},
	render(h) {
		return (
			<div class={`annotation-cell ${this.$data._expandable ? 'expandable' : ''}`} onClick={this.expand}>
				{ this.$slots.default }
			</div>
		)
	},
	methods: {
		expand() {
			this.$el.classList.add("expanded");
		}
	}
}

export default {
	install: (Vue, options) => {
		Vue.mixin({
			components: {
				"storeui-navbar": storeUINavigationBar,
				"storeui-collection-view-cell": storeUIHorizontalCollectionViewCell,
				"storeui-expandable-text": storeUIExpandableText,
				"storeui-annotation-cell": storeUIAnnotationCell
			}
		});
	}
}