				<!--<div class="product-review-actions-cell">
					<router-link class="review-action compose-review" :to="`/package/${packageData.identifier}/compose-review`" :target="isCydia ? '_blank' : ''">Write a Review</router-link>
				</div>-->
				
				<div class="title-header-view" v-if="isUpdated">
