				</div>
				<div class="annotation-collection">
					<storeui-annotation-cell>
						<p class="title-label">Seller</p>
						<p class="subtitle-label">FESTIVAL Development</p>
					</storeui-annotation-cell>
					<storeui-annotation-cell>
						<p class="title-label">Size</p>
						<p class="subtitle-label">{{ filesize(packageData.latestVersion.raw.Size) }}</p>
					</storeui-annotation-cell>
					<storeui-annotation-cell>
						<p class="title-label">Category</p>
						<p class="subtitle-label">{{ packageData.latestVersion.raw.Section }}</p>
					</storeui-annotation-cell>
					<storeui-annotation-cell :expandable="true">
						<p class="title-label">Compatibility</p>
						<p class="subtitle-label">Requires iOS {{ packageData.minOSVersion }}</p>
						<div class="annotation-cell-item">
							<p class="text-label">Requires iOS {{ packageData.minOSVersion }} or later. Compatible with up to iOS {{ packageData.maxOSVersion }}.</p>
						</div>
					</storeui-annotation-cell>

					<storeui-annotation-cell :expandable="dependencies.length > 1" v-if="dependencies && dependencies.length > 0">
						<p class="title-label">Dependencies</p>
						<p class="subtitle-label">
							{{ dependencies[0].replace(/\(.*\)/, '') }}
							<span v-if="dependencies.length > 1">and {{ dependencies.length - 1}} other{{ (dependencies.length - 1) > 1 ? "s" : ""}}</span>
						</p>

						<div class="annotation-cell-item" v-for="dependency in dependencies">
							<p class="text-label">{{ dependency.replace(/\(.*\)/, '') }}</p>
							<p class="sub-text-label" v-if="(/^.*\((.*)\)$/).test(dependency)">{{ dependency.match(/^.*\((.*)\)$/)[1] }}</p>
						</div>
					</storeui-annotation-cell>
					
					<storeui-annotation-cell :expandable="conflicts.length > 1" v-if="conflicts && conflicts.length > 0">
						<p class="title-label">Conflicts</p>
						<p class="subtitle-label">
							{{ conflicts[0].replace(/\(.*\)/, '') }}
							<span v-if="conflicts.length > 1">and {{ conflicts.length - 1}} other{{ (conflicts.length - 1) > 1 ? "s" : ""}}</span>
						</p>

						<div class="annotation-cell-item" v-for="conflict in conflicts">
							<p class="text-label">{{ conflict.replace(/\(.*\)/, '') }}</p>
							<p class="sub-text-label" v-if="(/^.*\((.*)\)$/).test(conflict)">{{ conflict.match(/^.*\((.*)\)$/)[1] }}</p>
						</div>
					</storeui-annotation-cell>
				</div>
				
				<div class="product-page-link-collection">
					<a class="product-page-link-cell developer-website" href="https://festival.ml" target="_blank">
						<p class="title-label">Developer Website</p>
					</a>

					<a class="product-page-link-cell privacy-policy" href="https://festival.ml/impressum/" target="_blank">
						<p class="title-label">Privacy Policy</p>
					</a>
				</div>

				<div class="footnote-cell" v-if="!isCydia">
					<p>&copy; 2018 Team FESTIVAL</p>
				</div>
			</div>
		</div>
	</div>
</template>

