<script>
import { AccountAPI, AuthAPI, PackageAPI } from '@/scripts/ApiUtil'

import ProductLockupBadgeStack from '@/components/ProductLockupBadgeStack.vue'
import ProductMediaCell from '@/components/ProductMediaCell.vue'
import ProductRatingCell from '@/components/ProductRatingCell.vue'
import ProductReviewCell from '@/components/ProductReviewCell.vue'

export default {
	name: "Package",
	components: {
		ProductLockupBadgeStack,
		ProductMediaCell,
		ProductRatingCell,
		ProductReviewCell
	},
	data() {
		return {
			packageData: null,
			screenshotData: null,
			ratingData: null,
			reviewData: null,
			dependencies: null,
			conflicts: null,
			currentAccountData: null,
			
			deviceInfo: (/i(Phone|Pad)/).test(navigator.userAgent) ? navigator.userAgent.match(/i(Phone|Pad)/)[0].toLowerCase() : null,
			isCydia: (/Cydia/).test(navigator.userAgent),
			isUpdated: false,
			
			location: window.location
		}
	},
	async created() {
		let _packageData = await PackageAPI.getPackage(this.$route.params.packageId);
		if (_packageData.status == 200) {
			this.packageData = _packageData.data;
			this.screenshotData = JSON.parse(this.packageData.screenshots);
			this.dependencies = JSON.parse(this.packageData.latestVersion.raw["Depends"]);
			this.conflicts = JSON.parse(this.packageData.latestVersion.raw["Conflicts"]);
			
			let _versionData = await PackageAPI.getPackageVersions(this.$route.params.packageId);
			if (_versionData.status == 200) {
				// this.versionData = _versionData.data;
				this.isUpdated = _versionData.data.length > 1;
			}
			
			let _reviewData = await PackageAPI.getPackageReviews(this.$route.params.packageId);
			if (_reviewData.status == 200) {
				this.reviewData = _reviewData.data;
			}
			
			let _ratingData = await PackageAPI.getPackageRatings(this.$route.params.packageId);
			if (_ratingData.status == 200) {
				this.ratingData = _ratingData.data;
			}
		} else {
			this.packageData = {
				status: _packageData.status
			}
		}
	},
	mounted() {
		window.scrollTo(0,0);
		
		if (!this.isCydia) {
			window.addEventListener("scroll", this.didScroll);
			
			// if (window.scrollY > 88) {
			// 	packageTitleHeader.classList.remove("animate-out");
			// 	packageTitleHeader.classList.add("animate-in");
			// 	packageTitleHeader.classList.remove("hidden");

			// 	lockupAnimationContainer.classList.remove("animate-in");
			// 	lockupAnimationContainer.classList.add("animate-out");
			// } else {
			// 	packageTitleHeader.classList.remove("animate-in");
			// 	packageTitleHeader.classList.add("animate-out");
			// }
		}
	},
	destroyed () {
		window.removeEventListener("scroll", this.didScroll);
	},
	methods: {
		nl2br(text) {
			return text.replace(/(?:\r\n|\r|\n)/g, '<br />');
		},
		timeSince(timestamp) {
			let secondsPast = (new Date().getTime() - timestamp.getTime()) / 1000;
			if (secondsPast < 3600) {
				return "Today";
			}
			if (secondsPast < 86400) {
				return `${parseInt(secondsPast / 3600)}h ago`;
			}
			if (secondsPast < 604800) {
				return `${parseInt(secondsPast / 86400)}d ago`;
			}
			if (secondsPast < 2628000) {
				return `${parseInt(secondsPast / 604800)}w ago`;
			}
			if (secondsPast < 31536000) {
				return `${parseInt(secondsPast / 2628000)}mo ago`;
			}
			return `${parseInt(secondsPast / 31536000)}y ago`;
		},
		filesize(size) {
			if (isNaN(size)) { size = 0 }
			if (size < 1024) { return size + ' B' }
			if ((size /= 1024) < 1024) { return size.toFixed(0) + ' KB' }
			if ((size /= 1024) < 1024) { return size.toFixed(2) + ' MB' }
		},
		didScroll() {
			const packageTitleHeader = this.$refs["packageTitleHeader"];
			const lockupAnimationContainer = this.$refs["lockupAnimationContainer"];
		
			if (window.scrollY > 88) {
				packageTitleHeader.classList.remove("animate-out");
				packageTitleHeader.classList.add("animate-in");
				packageTitleHeader.classList.remove("hidden");

				lockupAnimationContainer.classList.remove("animate-in");
				lockupAnimationContainer.classList.add("animate-out");
			} else {
				packageTitleHeader.classList.remove("animate-in");
				packageTitleHeader.classList.add("animate-out");

				if (lockupAnimationContainer.classList.contains("animate-out")) {
					lockupAnimationContainer.classList.remove("animate-out");
					lockupAnimationContainer.classList.add("animate-in");
				}
			}
		}
	}
}
</script>