<script>
import { AccountAPI, PackageAPI } from '@/scripts/ApiUtil'
import dateformat from "dateformat";

export default {
	name: "ReviewDetails",
	data() {
		return {
			packageData: null,
			reviewData: null,
			reviewAccountData: null,
			isCydia: (/Cydia/).test(navigator.userAgent),
		}
	},
	async created() {
		let _packageData = await PackageAPI.getPackage(this.$route.params.packageId);
		if (_packageData.status == 200) {
			this.packageData = _packageData.data;
			
			let _reviewData = await PackageAPI.getPackageReview(this.$route.params.packageId, this.$route.params.reviewId);
			if (_reviewData.status == 200) {
				this.reviewData = _reviewData.data;
				
				let _reviewAccountData = await AccountAPI.getUser(this.reviewData.accountId);
				if (_reviewAccountData.status == 200) {
					this.reviewAccountData = _reviewAccountData.data;
				}
			} else {
				this.reviewData = {
					status: _reviewData.status
				}
			}
		} else {
			this.packageData = {
				status: _packageData.status
			}
		}
	},
	mounted() {
		window.scrollTo(0,0);
	},
	methods: {
		dateformat: dateformat,
		getInitials(name) {
			var initials = name.replace(/\_|\:|\.|\:/g, " ").match(/\b\w/g) || [];
			initials = ((initials.shift() || '') + (initials.pop() || '')).toUpperCase();
			
			return initials;
		},
		getTimestampAvailable(message) {
			const messages = this.reviewData.messages;
			const index = messages.indexOf(message);
			
			if (!messages[index-1] || (new Date(messages[index].createdAt).getTime()/1000) - (new Date(messages[index-1].createdAt).getTime()/1000) >= 7200) {
				return true;
			}
			return false;
		},
		getMessageClassName(message) {
			const messages = this.reviewData.messages;
			const index = messages.indexOf(message);
			const classes = [];
			
			classes.push(messages[index].fromDeveloper ? "message-received" : "message-sent");
			
			if (!messages[index-1] || messages[index-1].fromDeveloper != messages[index].fromDeveloper) {
				classes.push("message-first");
			}
			
			if (!messages[index+1] || messages[index+1].fromDeveloper != messages[index].fromDeveloper) {
				classes.push("message-last", "message-tail");
			}
			
			return classes.join(" ");
		}
	}
}
</script>