import { get, post, put, delete as _delete } from "axios";

const apiUrl = `${window.location.origin}/api`
// const apiUrl = `http://localhost:64031/api`

export class AuthAPI {
	static async register(registerData) {
		return await post(`${apiUrl}/auth/register`, registerData).catch(_ => _.response);
	}
	
	static async login(loginData) {
		return await post(`${apiUrl}/auth/login`, loginData).catch(_ => _.response);
	}
	
	static async verify() {
		return await get(`${apiUrl}/auth/verify`, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
}

export class AccountAPI {
	static async getMe() {
		return await get(`${apiUrl}/account/me`, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	static async getUser(userId) {
		return await get(`${apiUrl}/account/${userId}`).catch(_ => _.response);
	}
	
	static async updateMe(updateData) {
		return await put(`${apiUrl}/account/me`, updateData, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	static async deleteMe() {
		return await _delete(`${apiUrl}/account/me`, updateData, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	static async uploadProfileImage(file) {
		const formData = new FormData();
		formData.append("file", file);
		
		return await post(`${apiUrl}/account/profileImage`, formData, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`,
				"Content-Type": `multipart/form-data, boundary=${formData._boundary}`
		}
		}).catch(_ => _.response);
	}
}

