export class PackageAPI {
	// - Packages (General)
	static async getPackages() {
		return await get(`${apiUrl}/packages/`).catch(_ => _.response);
	}

	// - Packages (Specific)
	static async getPackage(packageId) {
		return await get(`${apiUrl}/packages/${packageId}`).catch(_ => _.response);
	}
	
	// - Versions (General)
	static async getPackageVersions(packageId) {
		return await get(`${apiUrl}/packages/${packageId}/versions`).catch(_ => _.response);
	}
	
	static async getPackageLatestVersion(packageId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/latest`).catch(_ => _.response);
	}
	
	// - Versions (Specific)
	static async getPackageVersion(packageId, versionId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/${versionId}`).catch(_ => _.response);
	}
	
	// - Ratings (General)
	static async getPackageRatings(packageId) {
		return await get(`${apiUrl}/packages/${packageId}/ratings`).catch(_ => _.response);
	}

	static async getPackageLatestVersionRatings(packageId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/latest/ratings`).catch(_ => _.response);
	}

	static async getPackageVersionRatings(packageId, versionId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/${versionId}/ratings`).catch(_ => _.response);
	}

	static async getPackageReviewRating(packageId, reviewId) {
		return await get(`${apiUrl}/packages/${packageId}/reviews/${reviewId}/rating`).catch(_ => _.response);
	}

	// - Reviews (General)
	static async getPackageReviews(packageId) {
		return await get(`${apiUrl}/packages/${packageId}/reviews`).catch(_ => _.response);
	}
	
	static async getPackageLatestVersionReviews(packageId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/latest/reviews`).catch(_ => _.response);
	}
	
	static async getPackageVersionReviews(packageId, versionId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/${versionId}/reviews`).catch(_ => _.response);
	}
	
	static async addReview(packageId, reviewData) {
		return await get(`${apiUrl}/packages/${packageId}/reviews/add`, reviewData, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	// - Reviews (Specific)
	static async getPackageReview(packageId, reviewId) {
		return await get(`${apiUrl}/packages/${packageId}/reviews/${reviewId}`).catch(_ => _.response);
	}
	
	static async getPackageVersionReview(packageId, versionId, reviewId) {
		return await get(`${apiUrl}/packages/${packageId}/versions/${versionId}/reviews/${reviewId}`).catch(_ => _.response);
	}
	
	static async addReviewMessage(packageId, reviewId, messageData) {
		return await post(`${apiUrl}/packages/${packageId}/reviews/${reviewId}/addMessage`, messageData, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	static async updateReviewMessage(packageId, reviewId, messageId, messageData) {
		return await post(`${apiUrl}/packages/${packageId}/reviews/${reviewId}/${messageId}`, messageData, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	static async deleteReview(packageId, reviewId) {
		return await _delete(`${apiUrl}/packages/${packageId}/reviews/${reviewId}`, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
	
	static async deleteReviewMessage(packageId, reviewId, messageId) {
		return await _delete(`${apiUrl}/packages/${packageId}/reviews/${reviewId}/${messageId}`, {
			headers: {
				"Authorization": `Bearer ${window.$cookies.get("authToken")}`
			}
		}).catch(_ => _.response);
	}
}

