<template>
	<div>
		<div v-if="!packageData">
			<div class="loading-screen">
				<div class="loading-indicator-wrapper">
					<div class="loading-indicator" />
					<p class="loading-indicator-label">Loading</p>
				</div>
			</div>
		</div>

		<div v-if="packageData && packageData.status == 404">
			<div class="loading-screen">
				<div class="loading-indicator-wrapper">
					<p class="error-message">Package not found</p>
					<p class="error-subtitle">The package "{{ $route.params.packageId }}" could not be found.</p>
				</div>
			</div>
		</div>
		
		<div v-if="packageData && !packageData.status">
			<vue-headful :title="isCydia ? 'Ratings & Reviews' : `${packageData.name}  Ratings & Reviews`" />
			<storeui-navbar title="Ratings &amp; Reviews" v-if="!isCydia"></storeui-navbar>
			
			<div class="collection-view">
				<ProductRatingCell :ratingData="ratingData" v-if="reviewData && reviewData.length" />
				
				<p class="status-label" v-if="!reviewData || !reviewData.length">This package hasn't received enough ratings or reviews to display a summary.</p>
				
				<div class="title-header-view">
					<p class="title-label"></p>
					<!--<router-link class="review-action compose-review" :to="`/package/${packageData.identifier}/compose-review`" :target="isCydia ? '_blank' : ''">Write a Review</router-link>-->
				</div>
				
				<div class="product-review-collection-cell " v-if="reviewData && reviewData.length">
					<ProductReviewCell v-for="(reviewObj, index) in reviewData" :reviewObj="reviewObj" :packageId="packageData.identifier" :key="reviewObj.id" />
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { PackageAPI } from '@/scripts/ApiUtil'

import ProductRatingCell from '@/components/ProductRatingCell.vue'
import ProductReviewCell from '@/components/ProductReviewCell.vue'

export default {
	name: "Reviews",
	components: {
		ProductRatingCell,
		ProductReviewCell
	},
	data() {
		return {
			packageData: null,
			reviewData: null,
			ratingData: null,
			isCydia: (/Cydia/).test(navigator.userAgent),
		}
	},
	async created() {
		let _packageData = await PackageAPI.getPackage(this.$route.params.packageId);
		if (_packageData.status == 200) {
			this.packageData = _packageData.data;
			
			let _reviewData = await PackageAPI.getPackageReviews(this.$route.params.packageId);
			if (_reviewData.status == 200) {
				this.reviewData = _reviewData.data;
			}
			
			let _ratingData = await PackageAPI.getPackageRatings(this.$route.params.packageId);
			if (_ratingData.status == 200) {
				this.ratingData = _ratingData.data;
			}
		} else {
			this.packageData = {
				status: _packageData.status
			}
		}
	},
	mounted() {
		window.scrollTo(0,0);
	}
}
</script>