<template>
	<div>
		<div v-if="!packageData">
			<div class="loading-screen">
				<div class="loading-indicator-wrapper">
					<div class="loading-indicator" />
					<p class="loading-indicator-label">Loading</p>
				</div>
			</div>
		</div>

		<div v-if="packageData && packageData.status == 404">
			<div class="loading-screen">
				<div class="loading-indicator-wrapper">
					<p class="error-message">Package not found</p>
					<p class="error-subtitle">The package "{{ $route.params.packageId }}" could not be found.</p>
				</div>
			</div>
		</div>
		
		<div v-if="packageData && !packageData.status">
			<vue-headful :title="isCydia ? 'Version History' : `${packageData.name}  Version History`" />
			<storeui-navbar title="Version History" v-if="!isCydia"></storeui-navbar>
			
			<div class="collection-view">
				<div class="titled-paragraph-cell detail-styling" v-for="versionObj in versionData">
					<div class="row">
						<p class="primary-subtitle">{{ versionObj.version }}</p>
						<p class="secondary-subtitle">{{ timeSince(new Date(versionObj.createdAt)) }}</p>
					</div>
					<storeui-expandable-text :startExpanded="isCydia">
						<p v-html="versionObj.changeText" />
					</storeui-expandable-text>
					<div class="separator-view"></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import { PackageAPI } from '@/scripts/ApiUtil'

export default {
	name: "Changelog",
	data() {
		return {
			packageData: null,
			versionData: null,
			isCydia: (/Cydia/).test(navigator.userAgent),
		}
	},
	async created() {
		let _packageData = await PackageAPI.getPackage(this.$route.params.packageId);
		if (_packageData.status == 200) {
			this.packageData = _packageData.data;
			
			let _versionData = await PackageAPI.getPackageVersions(this.$route.params.packageId);
			if (_versionData.status == 200) {
				this.versionData = _versionData.data;
			}
		} else {
			this.packageData = {
				status: _packageData.status
			}
		}
	},
	methods: {
		timeSince(timestamp) {
			let secondsPast = (new Date().getTime() - timestamp.getTime()) / 1000;
			if (secondsPast < 3600) {
				return "Today";
			}
			if (secondsPast < 86400) {
				return `${parseInt(secondsPast / 3600)}h ago`;
			}
			if (secondsPast < 604800) {
				return `${parseInt(secondsPast / 86400)}d ago`;
			}
			if (secondsPast < 2628000) {
				return `${parseInt(secondsPast / 604800)}w ago`;
			}
			if (secondsPast < 31536000) {
				return `${parseInt(secondsPast / 2628000)}mo ago`;
			}
			return `${parseInt(secondsPast / 31536000)}y ago`;
		}
	}
}
</script>