<template>
	<div class="product-lockup-badge-stack" v-if="ratingData">
		<div class="badge-view no-padding text-left" v-if="!ratingData.total">
			<div class="review-rating-view not-enough-ratings" />
			<p class="caption-label">Not enough ratings</p>
		</div>
		
		<div class="badge-view text-left" v-if="ratingData.total">
			<p class="value-label">{{ ratingData.average.toLocaleString("en-us", { minimumFractionDigits: 1, maximumFractionDigits: 1 }) }}</p>
			
			<div class="review-rating-view">
				<div class="review-rating-star-wrapper" v-for="i in 5" :key="i">
					<div class="background-fill" :style="{'width': `${indexPercentage(ratingData.average, i - 1)}px`}" />
					<div class="review-rating-star" />
				</div>
			</div>
			
			<p class="caption-label">{{ ratingData.total.toLocaleString("en-US") }} Rating{{ ratingData.total != 1 ? 's' : '' }}</p>
		</div>
		
		<div class="badge-view text-right">
			<p class="value-label">{{ downloadCount.toLocaleString("en-us") }}</p>
			<p class="caption-label">Download{{ downloadCount != 1 ? 's' : '' }}</p>
		</div>
	</div>
</template>

<script>
export default {
	name: "ProductLockupBadgeStack",
	props: ["ratingData", "downloadCount"],
	methods: {
		indexPercentage(average, index) {
			return Math.max(Math.min(average - index, 1), 0) * 16;
		}
	}
}
</script>